@import "../../scss/variables";
@import "../../scss/mixins";


.navbar{
    background-color: $cardNavBgColor;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    z-index: 4;
    width: 100%;
    top: 0;

    .links{
        display: flex;
        font-size: 1.3rem;
        justify-content: flex-end;
        gap: 1%;

        a{
            color: rgb(226, 11, 11);
            text-decoration: none;
            text-transform: uppercase;
            border-radius: 15px;
            padding: 0.7rem;
            &:hover{
                background-color:burlywood;
                color: $cardNavBgColor;
                transition: 0.2s ease-in-out;
            }
        }
    }
    img{
        width: 150px;
        height: 75px;
        border-radius:50%;
        margin: 8px;
    }
}

@include media-xsm{
    .links{
        flex-direction: column;
    }
}
@include media-lg{
    .links{
        flex-direction: row;
    }
}