@import "../../scss/variables";

.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 3rem;
    padding: 1.5rem;

    .cards{
        width: 500px;
        height: 350px;
        position: relative;
        overflow: hidden;
        &:hover .card-over{
            transform: translateY(0%);
        }
        &:hover img{
            opacity: 1;
        }

        .title{
            position: absolute;
            top: 0;
            left:0;
            background-color: rgba(77, 6, 65, 0.8);
            color: green;
            padding: 0.5rem;
            border-radius: 10px 10px 10px 10px;
            z-index: 2;
            font-size: 0.4rem;
        }

        .date{
            position: absolute;
            right: 0%;
            background-color: rgba(0,0,0,0.8);
            color: green;
            padding: 0.5rem;
            font-size: 0.4rem;
            border-radius: 10px 10px 10px 10px;;
            z-index: 2;
        }
        img{
            width: 500px;
            height: 350px;
            object-fit: cover;
            border-radius: 10px;
            opacity: 0.7;
        }
        .card-over{
            width: 500px;
            position: absolute;
            bottom: 0;
            left: 0;
            background-color: rgba(0,0,0,0);
            font-size: 1rem;
            padding: 1rem;
            border-radius: 10px;
            transform: translateY(100%);
            transition: transform 0.7s ease-in-out;
            z-index: 3;
            max-height: 57%;
            text-align: justify;
            overflow: auto;

            &::-webkit-scrollbar{
                display: none;
            }
        }
    }
}











