@import '../../scss/variables';

.header{
    text-align: center;
    margin: 0.4rem;
    padding: 0.5rem;
    margin-top: 9rem;
    margin-bottom: 4rem;

    .text-shadow{
        color:red;
        font-size: 3rem;
        text-transform: uppercase;
        letter-spacing: 0.1em;
        animation: flicker 0.5s ease-in-out infinite alternate;
        transition: all 0.5s ease-in-out;

        &:hover{
            color: $cardNavBgColor;
            cursor: pointer;
        }
    }

    @keyframes flicker {
        0%{
            opacity: 0.5;
            text-shadow: 5px 5px 10px $cardNavBgColor;
        }
        100%{
            opacity: 1;
            text-shadow: 5px 5px 10px $cardNavBgColor;
        }
        
    }
}